import React, { Component } from 'react';
import './App.css';
import { Link } from "react-router-dom";


class Main extends Component {
    state = {
        //하단 값들을 초기화
        selectedFile: null,
        imagePreviewUrl: null
    };
    //사진을 올릴때 사용하는 함수
    fileChangedHandler = event => {
        this.setState({
            selectedFile: event.target.files[0]
        })

        let reader = new FileReader();

        reader.onloadend = () => {
            this.setState({
                imagePreviewUrl: reader.result
            });
        }

        reader.readAsDataURL(event.target.files[0])

    }
    //사진을 서버에 업로드할때 사용하는 함수
    submit = () => {
        var fd = new FormData();
        fd.append('file', this.state.selectedFile);
        var request = new XMLHttpRequest();
        request.onreadystatechange = function () {
            if (this.readyState === 4 && this.status === 200) {
                alert('분석을 시작합니다!');
            }
        };
        request.open("POST", "https://us-central1-tutorial-e6ea7.cloudfunctions.net/fileUpload", true);
        request.send(fd);
    }

    render() {
        //이미지 미리보기를 설정하는 부분
        let $imagePreview = (<div className="previewText image-container" ></div>);
        if (this.state.imagePreviewUrl) {
            $imagePreview = (<div className="image-container" ><img src={this.state.imagePreviewUrl} alt="icon" width="200" /> </div>);
        }


        return (
            //여기서부터 html같은 부분
            <div className='page'>
                <div className='container'>
                    {/* 제목부분 */}
                    <h1 className="heading">강아지 맞춤형 AI 서비스</h1>
                    <h2 className="heading2">나 누구개</h2>
                    {/* 왼쪽 설명 부분 */}
                    <div className="smallcontainer">
                        <p className="explain1">등록된 견종</p>
                        <p className="explain2">130종</p>
                        <p className="explain1"> 사용중인 애견인</p>
                        <p className="explain2">100만명</p>
                    </div>
                    {/* 오른쪽 파일 업로드, 이미지 전달 및 secondpage 링크 부분 */}
                    <div className="smallcontainer2">
                        <div className="img-holder">
                            <input type="file" name="avatar" onChange={this.fileChangedHandler} />
                            {/* 이미지 미리보기 부분을 여기서 호출 */}
                            {$imagePreview}
                        </div>

                        <Link to="/Subpage" className="goSubpage">
                            <button type="button" onClick={this.submit} >
                                찾아보기
                            </button>
                        </Link>


                    </div>
                </div>
            </div>
        );
    }
}

export default Main;