import React, { useState, useEffect } from 'react';
import './App.css';
import Chart from 'react-google-charts';
import axios from 'axios';

function Second() {
    //받아올 변수들을 usestate를 사용하여 객체형으로 할당시켰습니다.
    const [dogs, setDogInfo] = useState({
        Sec_Dog_breed: '',
        Sec_hashtag1: '',
        Sec_hashtag2: '',
        Sec_hashtag3: '',
        Sec_Diagram1: '',
        Sec_Diagram2: '',
        Sec_Diagram3: '',
        Sec_Diagram4: '',
        Sec_Diagram5: '',
        Sec_feature: '',
        Sec_warning: '',
        Sec_Vital_Status1: '',
        Sec_Vital_Status2: '',
        Sec_Vital_Status3: '',
        Sec_Vital_Status4: '',
    });
    //기본적으로 글자에 중첩되는 css를 여기서 써봤습니다. 나머지는 App.css에서 작업했습니다 
    let posts = { color: 'black', fontSize: '20px' };

    useEffect(() => {
        //값을 받아오는 부분입니다. 해당 서버로부터 값을 받아서 앞에서 지정한 setDogInfo함수에 지정했던 변수로 넣어줍니다.
        axios.get('http://785c0f644a7b.ngrok.io/')
            .then(({ data }) => setDogInfo(data));
    }, []);

    return (
        <div className="App">
            <div className="title">
                {/* 강아지 대문 사진 */}
                <div className="pre.img">
                    <img src="images/species/pre.jpg" alt="강아지 배경사진" />
                </div>
                {/* 강아지 이름 */}
                <div>
                    <span style={{ fontSize: '40px', color: 'black' }}>{dogs.Sec_Dog_breed}</span>{' '}
                </div>
                {/* 해시테그가 여기서 들어갑니다 */}
                <div className="Hashtag">
                    <p>{dogs.Sec_hashtag1}{dogs.Sec_hashtag2}{dogs.Sec_hashtag3}</p>
                </div>
            </div>

            <div className="body">
                <div className="dia">
                    <h3 style={posts}> 항목별 강아지 역량 </h3>
                    <p>
                        {' '}
                        해당 지표에 대한 견종별 데이터를 분석해 막대그래프로 제공해드립니다!{' '}
                    </p>
                    {/* 차트 부분입니다 google chart를 이용했어요 */}
                    <div className="chart">
                        <Chart
                            width={'500px'}
                            height={'300px'}
                            chartType="BarChart"
                            loader={<div>Loading Chart</div>}
                            data={[
                                ['항목', '역량'],
                                ['적응성', dogs.Sec_Diagram1],
                                ['건강 관리도', dogs.Sec_Diagram2],
                                ['운동성', dogs.Sec_Diagram3],
                                ['훈련 가능성', dogs.Sec_Diagram4],
                                ['친근함', dogs.Sec_Diagram5],
                            ]}
                            options={{
                                chartArea: { width: '50%' },
                                hAxis: {
                                    title: '항목별 강아지 역량',
                                    minValue: 0,
                                },
                                vAxis: {
                                    title: '항목',
                                },
                            }}
                            rootProps={{ 'data-testid': '1' }}
                        />
                    </div>
                </div>

                {/* 특징 부분 */}
                <div className="feature">
                    <h3 style={posts}> 강아지 특징 </h3>
                    <p> {dogs.Sec_feature} </p>
                    <hr />
                </div>

                {/* 주의사항 부분 */}
                <div className="caution">
                    <h3 style={posts}> 강아지 주의사항 </h3>
                    <p> {dogs.Sec_warning} </p>
                    <hr />
                </div>
            </div>

            {/* Vital부분 */}
            <div className="vital">
                <h3 style={posts}> Vital Status </h3>
                <div className="dog">
                    <img src="/images/vital/dog.png" alt="견종 분류" /> <p style={posts}>견종 분류</p>
                    <p>{dogs.Sec_Vital_Status1}</p>
                </div>

                <div className="height">
                    <img src="/images/vital/height.png" alt="신장" /> <p style={posts}>신장</p>
                    <p>{dogs.Sec_Vital_Status2}</p>
                </div>

                <div className="weight">
                    <img src="/images/vital/weight.png" alt="무게" /> <p style={posts}>무게</p>
                    <p>{dogs.Sec_Vital_Status3}</p>
                </div>

                <div className="life">
                    <img src="/images/vital/life.png" alt="기대 수명" /> <p style={posts}>기대 수명</p>
                    <p>{dogs.Sec_Vital_Status4}</p>
                </div>
                <hr />
            </div>
        </div>
    );
}
export default Second;