import React, { useState, useEffect } from 'react';
import './App.css';
import { Link } from "react-router-dom";
import axios from 'axios';

function Subpage() {
  let posts = { color: 'black', fontSize: '25px' };
  let postz = { color: 'black', fontSize: '20px' };


  const [dogs, rank] = useState({
    rank1: '',
    rank2: '',
    rank3: '',
  })

  useEffect(() => {
    axios.get('http://http://785c0f644a7b.ngrok.io/')
      .then(({ data }) => rank(data));
  }, []);

  return (
    <div className="App">

      <div className="first">
        <h1 style={posts}>높은 확률로 이 견종은</h1>
        {/* 제일 확률이 높은 견종 변수 기입 */}
        <p> {dogs.rank1} </p>
        <Link to="/First" className="goSubpage">
          <button type="button" /*onClick={this.submit}*/ style={postz}>눌러서 더 보기</button>
        </Link>

      </div>

      <div className="sec">
        <h1 style={posts}>2위</h1>
        {/* 두번째로 확률이 높은 견종 변수 기입 */}
        <p> {dogs.rank2} </p>
        <Link to="/Second" className="goSubpage">
          <button type="button" /*onClick={this.submit}*/ style={postz}>눌러서 더 보기</button>
        </Link>
      </div>

      <div className="third">
        <h1 style={posts}>3위</h1>
        {/* 세번째로 확률이 높은 견종 변수 기입 */}
        <p> {dogs.rank3} </p>
        <Link to="/Third" className="goSubpage">
          <button type="button" /*onClick={this.submit}*/ style={postz}>눌러서 더 보기</button>
        </Link>
      </div>

    </div>
  )
};

export default Subpage;
